.wrapper {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    min-width: 100px;
    padding: 9px 16px;
    font-size: 1.6rem;
    font-weight: 700;
    font-family: var(--font-family);
    border-radius: 4px;
    cursor: pointer;
    background-color: var(--white);
    border: 1px solid transparent;
    user-select: none;
    + .wrapper{
        margin-left: 8px;
    }
    &.disabled{
        pointer-events: none;
        opacity: 0.5;
    }
}

.icon + .title,
.title + .icon {
    margin-left: 8px;
}
.icon {
    display: inline-block;
    text-align: center;
    width: 24px;
}
.rounded {
    border-radius: 50px;
    box-shadow: 0 2px 8px rgb(0 0 0 / 6%);
    border-color: rgba(22,24,35,0.12);
    &:hover{
        border-color: rgba(22,24,35,0.2);
        background-color: rgba(22,24,35,0.03);
    }
}
// Button types
.primary {
    background-color: var(--primary);
    color: var(--white);
    border-color: var(--primary);
    &:hover {
        border-color: var(--primary);
        background: linear-gradient(0deg, rgba(0, 0, 0, 0.06), rgba(0, 0, 0, 0.06)), rgb(254, 44, 85);
    }
}
.outline {
    color: var(--primary);
    border-color: currentColor;
    &:hover {
        border-color: currentColor;
        background-color: rgba(254, 44, 85, 0.06);
    }
}
.basic {
    color: rgba(22, 24, 35, 0.34);
    background-color: rgba(22, 24, 35, 0.1);
}
.text {
    &:hover {
        text-decoration: underline;
    }
}
// Button sizes
.small {
    min-width: 88px;
    padding: 4px 16px;
}
.large {
    padding: 14px 16px;
    min-width: 140px;
}
